body {
  margin: 0;
  padding: 0;
  font-family: Georgia, serif;;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.center{
    margin: auto;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
}


.introduction{
    height: 330px;
    background-image: url('./images/boy.png');
    background-repeat: no-repeat;
    background-position: 20% 30px; 
    position: relative;
    background-size: 260px;
    background-color: #e0dac35c;
}
.description>p{
  font-size: 15px;
}
.band-name{
  width: 200px;
  margin: auto;
  margin-top:200px;
  margin-left: 45%;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-weight: 100;
  font-size: 4rem;
  text-align: center;
}

.first-name{
  color: #8a6d3b
}
.last-name{
  color: #333;
}
.description{
  padding: 40px;
  margin-top: 70px;
  margin-right: 20%;
}
.introduction-title{
  text-align: center;
}
.cd{
    height: 260px;
    background-image: url('./images/cd.png');
    background-repeat: no-repeat;
    background-position: 75% 30px;
    position: relative;
    background-size: 500px;
    background-color: #fff;
}
.song-details{
    padding-left: 50px;
}
.now-playing{
  padding: 3rem 0 1rem 0rem;

}
.song-name{
  color: #754c05bf;
}

.track {
  cursor: pointer;
}
.track-list li.track {
  background-color: #fff;
}
menu, ol, ul {
  list-style: none;
}

.track-list li.track .songs-track-head {
  display: table;
  table-layout: fixed;
  width: 100%;
  height: 30px;
}

.songs-column {
  display: table-cell;
  vertical-align: middle;
  padding: 0 10px;
  height: 30px;
  color: #754c05bf;
}

.glyphicon{
  opacity: 1
}
.songs-column-title{
  width: 50%;
}

.songs-column-tag{
  width: 30%;
}
.songs-column-duration{
  width: 20%;
}

.playlist-container{
  margin: 3rem;
}

.playlist-header{
  margin-left:50px;
}
.album-art{
  opacity: 0.4;
}
.playing{
  opacity: 1 ;
}
.songs-column-dot{
  width: 10%;
}
.songs-column-year{
  width: 10%;
  font-size: 2rem;
}
.songs-column-playlist{
  width: 45%;
  font-size: 2rem;
}
.discography{
  padding-left: 25px;
}
.glyphicon{
  color: #8a6d3b
}
.songs{
  margin: 0;
  padding: 0;
  border: 0;
  background: transparent;
}
.buy{
  color: #fff;
  margin: 0;
  padding: 5px 20px;
  border: 0;
  background: #8a6d3b;
}

.listen{
  color: #8a6d3b;
  margin: 0;
  padding: 5px 15px;
  border: 0;
  background: #8a6d3b36;
}
.playlist-column{
  display: table-cell;
  vertical-align: middle;
  padding: 0 10px;
  height: 50px;

}
.dot {
  height: 15px;
  width: 15px;
  background-color: #8a6d3b36;
  border-radius: 50%;
  display: block;
}
.active{
  background-color: #8a6d3b;
}